.popup-container {
    width: 350px;
    height: 500px;
    font-family: Arial, sans-serif;
    padding: 10px;
    box-sizing: border-box;
    overflow-y: auto;
  }
  
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .logo-section {
    display: flex;
    align-items: center;
    gap: 6px;
  }
  
  .logo {
    font-weight: bold;
    color: green;
    font-size: 1.1em;
  }
  
  .version {
    font-size: 0.8em;
    color: #555;
  }
  
  .tag {
    background: orange;
    color: white;
    padding: 2px 6px;
    border-radius: 3px;
    font-size: 0.75em;
  }
  
  .icons button {
    background: none;
    border: none;
    font-size: 1.2em;
    cursor: pointer;
  }
  
  .tabs {
    display: flex;
    margin-top: 10px;
  }
  
  .tab {
    flex: 1;
    padding: 8px;
    border: none;
    cursor: pointer;
    background-color: #f1f1f1;
  }
  
  .tab.active {
    background-color: #00b894;
    color: white;
    font-weight: bold;
  }
  
  .export-options {
    margin-top: 10px;
  }
  
  .export-options label {
    display: block;
    margin: 5px 0;
  }
  
  .export-btn {
    width: 100%;
    background-color: #00b894;
    color: white;
    border: none;
    padding: 10px;
    margin-top: 10px;
    cursor: pointer;
    border-radius: 4px;
  }
  
  .footer {
    margin-top: 10px;
    font-size: 0.7em;
    color: gray;
    text-align: center;
  }
  
  .not-whatsapp {
    text-align: center;
    margin-top: 30px;
    font-size: 0.9em;
  }
  